


if(document.querySelector('.review') === null) {

} else {
    const reviewBottomItem = document.querySelectorAll('.review__bottom-item');

    reviewBottomItem.forEach(item => {
        item.addEventListener('mouseover', () => {
            reviewBottomItem.forEach(el => {
                el.classList.remove('hover')
            })
            reviewBottomItem[0].classList.remove('hover')
            item.classList.add('hover');
        })
    })


    if (document.querySelector('.review__massage') === null) {

    } else {
        const reviewBox = document.querySelector('.review__massage');
        reviewIcon = reviewBox.querySelector('.review__massage-icon > picture'),
            reviewText = reviewBox.querySelector('.review__massage-text > p');

        if(reviewBox.classList.contains('bad')) {
            reviewBox.style.background = '#FFECEC';
            const source = reviewIcon.querySelector('source'),
                img = reviewIcon.querySelector('img');
            source.setAttribute('srcset', '/upload/review/massage-bad.svg');
            img.setAttribute('src', '/upload/review/massage-bad.svg');
            reviewText.innerHTML = 'Этот отзыв не влияет на рейтинг, так как вы не являетесь покупателем услуг данной охранной компании в рамках платформы <a href="">vincko:</a>'
        }
    }


    if(document.querySelector('.smile-input') === null) {

    } else {
        const input = document.querySelector('.smile-input'),
            smiles = document.querySelectorAll('.smile');

        input.addEventListener('input', () => {
            smiles.forEach((item, i) => {
                item.classList.remove('active');
                if(input.value < 10000) {
                    smiles[0].classList.add('active')
                } else if(input.value > 10000 && input.value <= 20000) {
                    smiles[1].classList.add('active')
                } else if(input.value > 20000 && input.value <= 30000) {
                    smiles[2].classList.add('active')
                } else if(input.value > 30000 && input.value <= 40000) {
                    smiles[3].classList.add('active')
                } else if(input.value > 40000 && input.value <= 50000) {
                    smiles[4].classList.add('active')
                }
            })
        })
    }






    const  stepTwoItems = document.querySelectorAll('.review__bottom-item');

    try {
        stepTwoItems.forEach(item => {
            const input = item.querySelector('.smile-input'),
                li = item.querySelectorAll('.review-item-step-2'),
                span = item.querySelector('.number-wrapper > span'),
                svg = item.querySelector('svg'),
                circle = item.querySelector('circle'),
                numberWrapper = item.querySelector('.number-wrapper');

            input.addEventListener('input', () => {
                li.forEach((el, i) => {
                    el.classList.remove('active')
                    if (input.value < 18000) {
                        li[0].classList.add('active');
                        span.innerHTML = '?';
                        svg.style.display = 'none';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                    } else if (input.value > 18000 && input.value < 28000) {
                        li[1].classList.add('active');
                        span.innerHTML = '0';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                        svg.style.display = 'none';
                    } else if (input.value > 28000 && input.value < 38000) {
                        li[2].classList.add('active');
                        span.innerHTML = '1';
                        svg.style.display = 'block';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                        circle.style.strokeDashoffset = '116';
                    } else if (input.value > 38000 && input.value < 48000) {
                        li[3].classList.add('active');
                        span.innerHTML = '2';
                        svg.style.display = 'block';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                        circle.style.strokeDashoffset = '87';
                    } else if (input.value > 48000 && input.value < 58000) {
                        li[4].classList.add('active');
                        span.innerHTML = '3';
                        svg.style.display = 'block';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                        circle.style.strokeDashoffset = '58';
                    } else if (input.value > 58000 && input.value < 68000) {
                        li[5].classList.add('active');
                        span.innerHTML = '4';
                        svg.style.display = 'block';
                        numberWrapper.style.border = '1px solid #D1DBE3';
                        circle.style.strokeDashoffset = '29';
                    } else if (input.value > 68000 && input.value < 78000) {
                        li[6].classList.add('active');
                        span.innerHTML = '5';
                        svg.style.display = 'none';
                        numberWrapper.style.border = '2px solid #005DFF';
                    }

                })
            })
        })
    }
    catch{};
    const content = document.querySelectorAll('.content');
    const stepThreeActiveBlocks = document.querySelectorAll('.content-wrapper .active-block');
    try {
        content.forEach((item, n) => {

            let notBefore = 0;
            const q = item.querySelectorAll('.q');

            q.forEach((elem, i) => {
                const input = elem.querySelector('.smile-input'),
                    li = elem.querySelectorAll('.review-item-step-3'),
                    span = elem.querySelector('span'),
                    btn = elem.querySelector('.next-btn'),
                    h5 = elem.querySelector('h5');

                input.addEventListener('input', () => {
                    li.forEach((el, i) => {
                        el.classList.remove('active')
                        if (input.value < 18000) {
                            li[0].classList.add('active')
                            span.innerHTML = '?'
                        } else if (input.value > 18000 && input.value < 28000) {
                            li[1].classList.add('active')
                            span.innerHTML = '0'
                        } else if (input.value > 28000 && input.value < 38000) {
                            li[2].classList.add('active')
                            span.innerHTML = '1'
                        } else if (input.value > 38000 && input.value < 48000) {
                            li[3].classList.add('active')
                            span.innerHTML = '2'
                        } else if (input.value > 48000 && input.value < 58000) {
                            li[4].classList.add('active')
                            span.innerHTML = '3'
                        } else if (input.value > 58000 && input.value < 68000) {
                            li[5].classList.add('active')
                            span.innerHTML = '4'
                        } else if (input.value > 68000 && input.value < 78000) {
                            li[6].classList.add('active')
                            span.innerHTML = '5'
                        }
                    })
                })


                elem.setAttribute('q-num', i);

                h5.addEventListener('click', () => {
                    q.forEach(trig => {
                        trig.classList.remove('active');
                    })
                    elem.classList.add('active')
                })

                btn.addEventListener('click', () => {
                    const nextBtn = document.querySelector('.next-btn-bottom')
                    notBefore += 1;
                    elem.classList.remove('active');
                    if (!q[i + 1]) {

                    } else {
                        q[i + 1].classList.remove('before')
                        q[i + 1].classList.add('active')
                    }
                    if (notBefore === q.length) {
                        nextBtn.disabled = false;
                    }
                })

            })
        })
    }
    catch {};
    const stepThreeItems = document.querySelectorAll('.step-3-item');
    const btn = document.querySelector('.next-btn-bottom');
    try {
        stepThreeItems.forEach((item, i) => {

            btn.addEventListener('click', () => {
                btn.disabled = true
                if (btn.classList.contains('step-2') && stepThreeItems[i + 1].classList.contains('active')) {
                    btn.classList.remove('step-2')
                    btn.classList.add('step-3')
                    stepThreeItems[i + 1].classList.remove('active')
                    stepThreeItems[i + 1].classList.add('pre-back')
                    stepThreeItems[i + 2].classList.add('active')
                    stepThreeItems[i + 2].classList.remove('pre-next')

                    const btnBlock = document.querySelector('.review__btn.step-3');
                    const next = btnBlock.querySelector('.next-btn-bottom');
                    const stop = btnBlock.querySelector('.stop');
                    const bonusBlock = stop.querySelector('.bonus-block');
                    const pic = bonusBlock.querySelector('picture');
                    const bonSpan = bonusBlock.querySelector('span');

                    next.style.display = 'none';
                    stop.style.display = 'flex';
                    bonusBlock.style.cssText = `
                display: flex;
                width: 112px;
            `;
                    pic.style.cssText = `
                margin-left: 20px;
            `;
                    bonSpan.style.color = '#FF6E52';
                }
            })
            btn.addEventListener('click', () => {
                if (btn.classList.contains('step-1')) {
                    btn.classList.remove('step-1')
                    btn.classList.add('step-2')
                    item.classList.remove('active')
                    item.classList.add('back')
                    stepThreeItems[i + 1].classList.add('active')
                    stepThreeItems[i + 1].classList.remove('next')
                }
            })
        })
    }
    catch{};


    if(document.body.clientWidth < 1098) {
        const reviewBlock = document.querySelector('.review');
        const stepOneNext = document.querySelector('.review__btn.step-1 .next');
        const stepTwoNext = document.querySelector('.review__btn.step-2 .next');
        const stepOneMid = document.querySelector('.review__mid-step-1.mob');
        const stepTwoMid = document.querySelector('.review__mid-step-2.mob');
        const stepThreeMid = document.querySelector('.review__mid-step-3.mob');

        stepOneNext.addEventListener('click', () => {
            stepOneMid.classList.remove('active')
            stepTwoMid.classList.add('active')
            reviewBlock.classList.add('step-2');
        })
        stepTwoNext.addEventListener('click', () => {
            stepTwoMid.classList.remove('active')
            stepThreeMid.classList.add('active')
            reviewBlock.classList.remove('step-2')
            reviewBlock.classList.add('step-3')
        })
    } else {

        const reviewBlock = document.querySelector('.review');
        const stepOneNext = document.querySelector('.review__btn.step-1 .next');
        const stepTwoNext = document.querySelector('.review__btn.step-2 .next');
        const stepOneMid = document.querySelector('.review__mid-step-1');
        const stepTwoMid = document.querySelector('.review__mid-step-2');
        const stepThreeMid = document.querySelector('.review__mid-step-3');
        try {
            stepOneNext.addEventListener('click', () => {
                stepOneMid.classList.remove('active')
                stepOneMid.innerHTML = `
            <picture class="pic">
                <source srcset="/upload/review/step-1-no-active.svg">
                <img src="/upload/review/step-1-no-active.svg" alt="good">
            </picture>
            <p><span>1</span> Общее впечатление</p>
        `
                stepTwoMid.classList.add('active')
                reviewBlock.classList.add('step-2');
                stepTwoMid.innerHTML = `
            <picture class="pic">
                <source srcset="/upload/review/step-2-active.svg">
                <img src="/upload/review/step-2-active.svg" alt="good">
            </picture>
            <p><span>2</span> Всего 3 вопроса</p>
            <picture class="icon">
                <source srcset="/upload/review/step-2-icon.svg">
                <img src="/upload/review/step-2-icon.svg" alt="good">
            </picture>
            <p class="bonus">+500 бонусов</p>
        `
            })
        }
        catch {};
        try {
            stepTwoNext.addEventListener('click', () => {
                stepTwoMid.classList.remove('active')
                stepTwoMid.innerHTML = `
        <picture class="pic">
        <source srcset="/upload/review/step-2-no-active.svg">
        <img src="/upload/review/step-2-no-active.svg" alt="good">
        </picture>
        <p><span>2</span> Всего 3 вопроса</p>
        <picture class="icon">
            <source srcset="/upload/review/step-2-icon.svg">
            <img src="/upload/review/step-2-icon.svg" alt="good">
        </picture>
        <p class="bonus">+500 бонусов</p>
        `
                stepThreeMid.classList.add('active')
                reviewBlock.classList.remove('step-2')
                reviewBlock.classList.add('step-3')
                stepThreeMid.innerHTML = `
        <picture class="pic">
        <source srcset="/upload/review/step-3-active.svg">
        <img src="/upload/review/step-3-active.svg" alt="good">
        </picture>
        <p><span>3</span> Немного подробнее</p>
        <picture class="icon">
            <source srcset="/upload/review/step-3-icon.svg">
            <img src="/upload/review/step-3-icon.svg" alt="good">
        </picture>
        <p class="bonus">+600 бонусов</p>
        `
            })
        }
        catch {};
    }

}




